import React, { Component } from "react";

export default class SignUp extends Component {
    render() {
        return (
            <form>
                <h3>Registrar</h3>

                <div className="form-group">
                    <label>Nombres</label>
                    <input type="text" className="form-control" placeholder="Nombres" />
                </div>

                <div className="form-group">
                    <label>Apellidos </label>
                    <input type="text" className="form-control" placeholder="Apellidos" />
                </div>

                <div className="form-group">
                    <label>Email</label>
                    <input type="email" className="form-control" placeholder="Ingrese email" />
                </div>

                <div className="form-group">
                    <label>Contraseña</label>
                    <input type="password" className="form-control" placeholder="Ingrese contraseña" />
                </div>

                <button type="submit" className="btn btn-dark btn-lg btn-block">Registrase</button>
            </form>
        );
    }
}